import React, { Component } from 'react';

import Modal from 'react-bootstrap/Modal';
import { AiFillCheckCircle } from 'react-icons/ai';
import { IconContext } from 'react-icons';
import styles from '../../scss/modalStyle.scss';
import Button from 'react-bootstrap/Button';

class Feedback extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <Modal
                size="sm"
                show={this.props.showModal}
                onHide={this.props.handleHide}
            >
                <Modal.Dialog className={styles.modalConfirm}>
                    <Modal.Body className={styles.modalContent}>
                        <Modal.Body className={styles.modalHeader}>
                             <IconContext.Provider
                                 value={{
                                     color: '#fff',
                                     size: '70px',
                                     className: styles.modalConfirmIconBox,
                                 }}
                             >
                                 <div>
                                     <AiFillCheckCircle />
                                 </div>
                             </IconContext.Provider>
                            <h4 className="mx-auto">Congratulations!</h4>
                        </Modal.Body>
                        <Modal.Body className={styles.body}>
                            <p className="text-center">That was the correct answer.</p>
                        </Modal.Body>
                        <Modal.Body className={styles.modalFooter}>
                            <Button
                                className={styles.button}
                                data-dismiss="modal"
                                variant="success"
                                block
                                size="lg"
                                onClick={this.props.handleHide}
                            >
                                OK
                            </Button>
                        </Modal.Body>
                    </Modal.Body>
                </Modal.Dialog>
            </Modal>
        );
    }
}

export default Feedback;
